  //     display: none !important;
  //   }

  .VIpgJd-ZVi9od-ORHb-OEVmcd {
    top: -40px !important;
  }

  //   .translated-ltr {
  //     margin-top: -40px !important;
  //   }

  .VIpgJd-ZVi9od-aZ2wEe-wOHMyf {
    display: none;
  }

  //   #goog-gt-tt {
  //     display: none !important;
  //   }
  .skiptranslate {
    // display: none !important;
  }
  .VIpgJd-ZVi9od-ORHb {
    display: none !important;
    table {
      display: none !important;
    }
  }
}
